  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
}
.top-header-icon-block.searchform form span:hover {
  background-color: lightgray;
  color: #000;
}
.top-header-icon-block.searchform form input {
  width: 100%;
  padding: 10px;
  outline: none;
  border: none;
  border-bottom: 1px solid #000;
  border-radius: 0;
  font-size: 16px;
  font-weight: 600;
  color: #000;
  background-color: transparent;
  font-family: "Vultura", sans-serif;
}
